  <div class="hello">
    <div class="top" :style="{ background: 'url(' + topBg + ')' }"></div>

    <div class="banner_div">
      <div class="bg1">
        <img src="../../static/img/1.gif" />
      </div>
      <div class="bg2_div">
        <img src="../../static/img/labels.png" alt="" />
      </div>
    </div>

    <div class="slogan" id="slogan">
      <img src="../../static/img/slogan.png" alt="" />
    </div>

    <div class="showBar_div">     
      <!-- 水平滚动 -->
      <!-- <van-notice-bar class="showBar" color="#B3000F" left-icon="https://fztj.oss-cn-hangzhou.aliyuncs.com/fztj1607073337776speaker.png" :scrollable="true">
