    </div>

    <!-- modal -->
    <div class="modal_div" v-show="modalStatus">
      <div class="modal_div_inner" :class="{ resetHeight: signUpModalStatus }">
        <img
          class="regModalClose_icon"
          :src="closeIconPath"
          alt=""
          v-show="modalCloseShow"
          @click="
            (modalStatus = false),
              (loginTel = ''),
              (smsCodeVal = ''),
              (authBtnTxt = '获取验证码')
          "
        />

        <div class="reg_modalInner" v-show="regModalStatus">
          <div class="titie1">手机验证</div>
          <div class="title2">
            短信验证码已发送至{{ userTel | hasRegTelFilter }}
          </div>
          <div class="dsp_flex getAuth_div">
            <input
              type="tel"
              class="authCode_ipt"
              placeholder="请输入短信验证码"
              v-model="smsCodeVal"
              maxlength="6"
            />
            <button
              class="getAuthBtn"
              @click="getSmsBtnFn"
              :disabled="getAuthBtnStatus"
            >
              {{ authBtnTxt }}
            </button>
          </div>
          <button class="ensureBtn" @click="loginBtnClkFn" ontouchstart>
            确定
          </button>
        </div>

        <!-- 报名成功 -->
        <div class="signUp_modalInner" v-show="signUpModalStatus">
          <p>您已报名成功，请保持电话畅通</p>
          <p>稍后将有工作人员与您取得联系</p>
          <button class="signUp_btn">{{ signUpSecond }}秒后自动关闭</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import topBg from "../../static/img/topBg.png";
import Vue from "vue";
import { Field, Radio, RadioGroup, Checkbox, Toast, NoticeBar, Swipe, SwipeItem } from "vant";
Vue.use(Field);
Vue.use(Radio);
Vue.use(Checkbox);
Vue.use(RadioGroup);
Vue.use(Toast);
Vue.use(NoticeBar);
Vue.use(Swipe);
Vue.use(SwipeItem);

function ToastFn(msg) {
  Toast({
    message: msg,
    position: "bottom",
  });
}

export default {
  name: "HelloWorld",
  data() {
    return {
      topBg: topBg,
      workerNum: 8102, // 基数8000 一周刷新一次 每次累加1-10随机数
      nameValue: "",
      telValue: null,
      radio: "",
      checked: false,
      privacyShow: false,
      closeIconPath: require("../../static/img/close_icon.png"),
      modalCloseShow: true,
      userTel: "",
      smsCodeVal: null,
      getAuthBtnStatus: false,
      authBtnTxt: "获取验证码",
      modalStatus: false, 
      regModalStatus: false, 
      typeStr: "",
      loginNameShow: false,
      getStorageUserName: "",
      regFormShow: true,
      hasRegModalStatus: false,
      hasRegTel: "",
      signUpSecond: 3,
      signUpModalStatus: false,
    };
  },
  created() {},
  mounted() {
    if (this.$route.query.type) {
      // wap pc 小程序
      this.typeStr = this.$route.query.type;
    }

    window.addEventListener("beforeunload", this.fn);

    // 判断是否登录
    if (window.localStorage.getItem("token")) {
      this.whetherLoginFn();
    }

    // 截止目前已有xx名普工入职
    let _this = this;
    let timer = setInterval(() => {
      _this.workerNum =
        Math.floor(Math.random() * (10 - 1 + 1) + 1) + _this.workerNum;
      // if (_this.workerNum > 1520) {
      //   clearInterval(timer);
      // }
    }, 604800000); // 1 周= 604800000 毫秒
  },
  filters: {
    hasRegTelFilter(data) {
      if (data) {
        return data.replace(/(\d{3})\d{4}(\d{4})/, "$1****$2");
      }
    },
  },
  methods: {
    regBtnClk() {
      if (!this.nameValue) {
        ToastFn("请填写姓名");
        return;
      } else {
        if (!/[\u4E00-\u9FA5\uF900-\uFA2D]/.test(this.nameValue)) {
          ToastFn("姓名请填写中文");
          return;
        }
      }

      if (!this.telValue) {
        ToastFn("请填写电话");
        return;
      } else {
        if (!/^1[3456789]\d{9}$/.test(this.telValue)) {
          ToastFn("手机号码不正确");
          return;
        }
      }

      if (!this.radio) {
        ToastFn("请选择入职时间");
        return false;
      }

      if (!this.checked) {
        ToastFn("请勾选用户协议");
        return false;
      }    

      this.modalStatus = true;
      this.regModalStatus = true;
      this.userTel = this.telValue;
      this.getSmsBtnFn();
    },

    // 获取验证码
    getSmsBtnFn() {
      this.yzmEnsureBtn();
    },

    yzmEnsureBtn() {
      let _this = this;
      this.$http
        .get("api/activity/sendSmsCode", {
          params: {
            mobile: this.telValue,
            MachineCodeID: "suibianla",
          },
        })
        .then((res) => {
          let { code, msg } = res.data;
          if (code === 10000) {
            var count = 60;
            var timer = setInterval(() => {
              count--;
              _this.authBtnTxt = count + "s";
              _this.getAuthBtnStatus = true; // disabled
              if (count === 0) {
                _this.authBtnTxt = "获取验证码";
                _this.getAuthBtnStatus = false;
                clearInterval(timer);
              }
            }, 1000);
            ToastFn("短信发送成功");
          } else {
            ToastFn(msg);
          }
        });
    },

    loginBtnClkFn() {
      if (this.smsCodeVal) {
        this.$http
          .post(
            `api/activity/shortMessageLoginForRed?mobile=${this.telValue}&code=${this.smsCodeVal}&type=${this.typeStr}&name=${this.nameValue}&latestEntryType=${this.radio}`
          )
          .then((res) => {
            let { code, msg, data } = res.data;
            if (code === 10000) {              
              window.localStorage.setItem("token", data);
              this.modalStatus = false;
              this.regFormShow = false;
              this.loginNameShow = true;
              this.getStorageUserName = this.nameValue;
              window.localStorage.setItem("userName", this.nameValue);

              this.regSuccessFn()
              
            } else {
              ToastFn(msg);
            }
          });
      } else {
        ToastFn("请输入短信验证码");
        return false;
      }
    },

    regSuccessFn() {
      this.signUpSecond = 3;

      let _this = this;
      this.modalStatus = true;
      this.signUpModalStatus = true;
      this.regModalStatus = false;
      this.hasRegModalStatus = false;

      let count = this.signUpSecond;
      var timer = setInterval(() => {
        count--;
        _this.signUpSecond = count;
        if (count === 0) {
          clearInterval(timer);
          _this.modalStatus = false;
        }
      }, 1000);
    },

    changeUserAccoutClk() {
      // 切换账号 回退到原先url
      let $type = this.$route.query.type;
      this.$router.push({
        path: this.$route.path,
        query: {
          type: $type,
        },
      });

      window.localStorage.setItem("token", "");
      window.localStorage.setItem("userName", "");

      this.whetherLoginFn(), (this.hasRegModalStatus = false);
      this.loginNameShow = false;
    },

    // 判断是否登录
    whetherLoginFn() {
      let $token = window.localStorage.getItem("token");
      this.$http
        .get("api/activity/isToken", {
          params: {
            token: $token,
          },
        })
        .then((res) => {
          let { code, data } = res.data;
          if (code === 10000) {
            this.modalStatus = false; // modal外层div
            this.hasRegModalStatus = true; // 已经登录
            this.regModalStatus = false; // 获取验证码1
            this.hasRegTel = data;
            (this.regFormShow = false), // 姓名表格
              (this.loginNameShow = true), // 登录成功 显示姓名
              (this.getStorageUserName = window.localStorage.getItem(
                "userName"
              ));
          } else {
            this.regFormShow = true; // 姓名表格
          }
        });
    },

    checkboxFn(e) {
      this.checked = e;
    },

    radioChangeFn(e) {
      this.radio = e;
    },

    privacyTxtClk() {
      this.privacyShow = true;
      this.stopScroll();
    },

    modalCloseClk() {
      this.privacyShow = false;
      this.canScroll();
    },

    stopScroll() {
      var mo = function (e) {
        e.preventDefault();
      };
      document.body.style.overflow = "hidden";
      document.addEventListener("touchmove", mo, false);
    },

    canScroll() {
      var mo = function (e) {
        e.preventDefault();
      };
      document.body.style.overflow = "";
      document.removeEventListener("touchmove", mo, false);
    },

    footerBtnClkFn() {
      if (window.localStorage.getItem("token")) {
        this.loginBtnClkFn()
      } else {
        ToastFn("请先完成注册哦~");

        let targetDom = document.getElementsByClassName("slogan")[0];
        const currentY =
          document.documentElement.scrollTop || document.body.scrollTop;
        this.scrollAnimation(currentY, this.findPosY(targetDom)[0]);
      }
    },

    fn() {
      window.scrollTo(0, 0);
    },

    // 获取到顶部距离
    findPosY(obj) {
      var top = 0;
      if (obj.offsetParent) {
        do {
          top += obj.offsetTop;
        } while ((obj = obj.offsetParent));
        return [top];
      }
    },

    scrollAnimation(currentY, targetY) {
      let needScrollTop = targetY - currentY;
      let _currentY = currentY;
      setTimeout(() => {
        const dist = Math.ceil(needScrollTop / 10);
        _currentY += dist;
        window.scrollTo(_currentY, currentY);
        if (needScrollTop > 10 || needScrollTop < -10) {
          this.scrollAnimation(_currentY, targetY);
        } else {
          window.scrollTo(_currentY, targetY);
        }
      }, 1);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



